export default function MeetTeam(props){

    const viewJourneyTag =props.viewJourneyTag
    const showViewJourneysTag= props.showViewJourneysTag
    return(
        <>
            <div className="teamContainer">
                <div className="teamWrapper">
                    <span className="meetTxt">{props.meet}</span>
                </div>
                <div className="teamSect">
                    <div className="tr1">
                        { viewJourneyTag ? <Images img={details.david.img}  role={details.david.role}  name={details.david.name} first={true}/> :  <Images img={details.david.img}  role={details.david.role}  name={details.david.name} first={false}/> }
                        { viewJourneyTag ? <Images img={details.arasen.img}  role={details.arasen.role}  name={details.arasen.name} first={true}/> :  <Images img={details.arasen.img}  role={details.arasen.role}  name={details.arasen.name} first={false}/> }
                        { viewJourneyTag ? <Images img={details.tiam.img}  role={details.tiam.role}  name={details.tiam.name} first={true}/> :  <Images img={details.tiam.img}  role={details.tiam.role}  name={details.tiam.name} first={false}/> }
                    </div>
                    <div className="tr2">
                        { viewJourneyTag ? <Images img={details.amos.img}  role={details.amos.role}  name={details.amos.name} first={true}/> :  <Images img={details.amos.img}  role={details.amos.role}  name={details.amos.name} first={false}/> }
                        { viewJourneyTag ? <Images img={details.rokia.img}  role={details.rokia.role}  name={details.rokia.name} first={true}/> :  <Images img={details.rokia.img}  role={details.rokia.role}  name={details.rokia.name} first={false}/> }
                        { viewJourneyTag ? <Images img={details.reggie.img}  role={details.reggie.role}  name={details.reggie.name} first={true}/> :  <Images img={details.reggie.img}  role={details.reggie.role}  name={details.reggie.name} first={false}/> }
                    </div>
                    {showViewJourneysTag ? <ShowViewJourneys /> : " " }

                </div>
            </div>
        </>
    )
}

export function ShowViewJourneys(){
    return(
        <>
            <div className="viewJ">
                <div className="viewCont">
                    <VisibilityIcon/>
                    <Link to = "/journey" className="linksLinked">View Our Journeys</Link>
                </div>
            </div>

        </>
    )


}